* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #f3e9e9;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  text-align: center;
  background-image: radial-gradient(circle, #fdbf1d 17%, #b43a51 25%, #4572fc 100%);
  padding: 50px;
}

[name="searchQuery"] {
  height: 35px;
  border-radius: 10px;
  margin: 4px;
}

.load-more, .load-more, [type="submit"] {
  color: bisque;
  height: 35px;
  background-image: linear-gradient(90deg, #88afac 0%, #090979 43%, #00d4ff 100%);
  border-radius: 10px;
  margin: 4px;
}

.load-more:hover, [type="submit"]:hover {
  cursor: pointer;
  background-image: linear-gradient(90deg, #9ea8a7 0%, #40405e 43%, #00d4ff 100%);
}

.load-more:active, [type="submit"]:active {
  color: #272018;
  background-image: linear-gradient(90deg, #cbd8d7 0%, #a3a3be 43%, #73e6fd 100%);
}

.load-more {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.isHiden {
  display: none;
}

.gallery {
  max-width: 1500px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 25px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.photo-card {
  width: 300px;
  height: 310px;
  background-color: bisque;
  border: 5px solid rgba(200, 255, 0, .5);
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 12px -1px 39px -16px rgba(0, 0, 0, .64);
}

.photo-card_Img {
  height: 220px;
  min-width: 100%;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  text-align: center;
  font-size: 15px;
  display: block;
}

